import api from './index.js'

export default {
  async uploadServices (data) {
    const res= api().post('/api/services', data)
    return res
  },
  async updateServices (data, id) {
    return api().put(`/api/services/${id}`, data)
  },
  async getOneServices (id) {
    return api().get(`/api/services/${id}`)
  },
  async getServices () {
    return api().get('/api/services')
  },
}